<template>
<el-row class="center">
	<el-row class="changeType">
		<el-button type="primary" @click="dialogVisible = true"></el-button>
	</el-row>
	<el-col :span="12" v-show="type==1">
		<h2></h2>
		<el-form ref="form2" 
			:model="form2"
			label-width="120px" style="margin:20px;width:60%;min-width:600px;">
			<el-form-item label="ID" prop="storeid">
				<el-input style="width:300px" v-model="form2.list[0].storeid"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="onSubmit('form2')"></el-button>
			</el-form-item>
		</el-form>
	</el-col>
	<el-col :span="12" v-show="type==2">
		<h2></h2>
		<el-form ref="form" :model="form" label-width="120px" style="margin:20px;width:60%;min-width:600px;">
			<el-form-item>
				<el-button style="width:150px" type="success" @click="addshopId()">  </el-button>
			</el-form-item>
			<el-form-item label="ID">
				<el-row style="marginBottom:20px" v-for="(item, index) in form.list" :key="index">
					<el-col :span="20">
						<el-input style="width:300px" v-model="item.storeid" placeholder="id"></el-input>
					</el-col>
					<el-col :span="4">
						<el-button type="danger" @click="removeShopId(index)"></el-button>
					</el-col>
				</el-row>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="submitPay('form')"></el-button>
				<!-- <el-button></el-button> -->
			</el-form-item>
		</el-form>
	</el-col>
	<el-dialog
		title=""
		:visible.sync="dialogVisible"
		:show-close="false"
		:close-on-click-modal="false"
		:close-on-press-escape="false"
		top="15%"
		width="30%">
		<el-row>
			<el-col>
				<el-radio-group v-model="type">
					<el-radio :label="1"></el-radio>
					<el-radio :label="2"></el-radio>
				</el-radio-group>
			</el-col>
		</el-row>
		<span slot="footer" class="dialog-footer">
			<!-- <el-button @click="dialogVisible = false"> </el-button> -->
			<el-button type="primary" @click="changetype(type)"> </el-button>
		</span>
		</el-dialog>
</el-row>
	
</template>

<script>
	import { bind, bindstoreid, alipay } from '../../api/index' 
	export default {
		data() {
			return {
				loading: false,
				dialogVisible: true,
				exitFileList: [],
				ispayShop:'',
				type:1,
				mobile:'',
				form2:{
					list:[
						{storeid: ''}
					],
					ispayShop: 0
				},
				form: {
					ispayShop: 1,
					list:[
						{storeid: ''}
					]
				}
			}
		},
		created() {
			let userInfo = JSON.parse(sessionStorage.getItem('userinfo'))
			this.mobile = userInfo.mobile
			console.log(this.mobile)
		},
		methods: {
			addshopId(){
				if(this.form.list.length == 10){
					this.$message.error('10ID');
				} else{
					this.form.list.push( {storeid: ''} )
				}
			},
			removeShopId(index){
				if(this.form.list.length == 1){
					this.$message.error('ID');
				} else{
					this.form.list.splice(index,1)
				}
			},
			onSubmit(form2) {
				this.$refs[form2].validate((valid) => {
					if (valid) {
						this.form2.mobile = this.mobile
						bindstoreid(this.form2).then((res)=>{
							if(res.code==200){
								this.$message({
									type:'success',
									message:''
								})
								this.$router.push({path:"/sku"})
							}else{
								this.$message({
									type:'error',
									message:res.msg
								})
							}
						})

					} else {
						console.log('error submit!!');
						return false;
					}

				});

			},
			submitPay(form){
				this.form.mobile = this.mobile
					bindstoreid(this.form).then((res)=>{
						if(res.code==200){
							this.$message({
								type:'success',
								message:''
							})
							this.$router.push({path:"/main"})
						}else{
							this.$message({
								type:'error',
								message:res.msg
							})
						}
					})

			},
			//
			changetype(type){
				if(type == 2){
					alipay({mobile:this.mobile}).then((res)=>{
						console.log(res)
						let routerData = this.$router.resolve({path:'/pay',query:{htmls:res}})
	        	this.htmls = res
	        	//
	        	window.location.href = routerData.href
					})
				} else{
					bind({mobile:this.mobile,bind:type}).then((res)=>{
						if(res.code==200){
							this.$message({
								type:'success',
								message: res.msg
							})
							this.ispayShop = false
							this.dialogVisible = false
						}
					})
				}
			}
		}
	}
</script>
<style lang="scss" scoped>
	.center{
		height: 100%;
	}
	.changeType{
		padding: 20px;
	}
</style>


