<template>
  <div class="login">
    <h1 class="headertitle"></h1>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-position="left"
      label-width="100px"
      class="demo-ruleForm login-container"
    >
      <h3 class="title"></h3>
      <el-form-item prop="mobile" label="">
        <el-input type="text" v-model.number="ruleForm.mobile" auto-complete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="code" label="">
        <el-input
          type="text"
          style="width:140px"
          v-model.number="ruleForm.code"
          auto-complete="off"
          placeholder=""
        ></el-input>
        <el-button
          style="width:100px"
          type="primary"
          :disabled="isdisable"
          @click="getCode()"
        >{{this.btnmsg}}</el-button>
      </el-form-item>
      <el-form-item style="width:100%;">
        <el-button
          type="primary"
          style="width:100%;"
          @click.native.prevent="handleSubmit2"
          :loading="logining"
        ></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import { requestLogin } from "../api/api";
import { apisms, login } from "../api/index";
//import NProgress from 'nprogress'
export default {
  data() {
    return {
      logining: false,
      isdisable: false,
      numberCode: 60,
      btnmsg: "",

      ruleForm: {
        mobile: "",   
        code: ""
      },
      rules: {
        mobile:[
          {type:'number',required:true,message: '', trigger: 'blur'}
        ],
        code:[
          {required:true, trigger: 'blur',message: ''}
        ]
      },
      checked: true
    };
  },
  methods: {
    handleReset2() {
      this.$refs.ruleForm.resetFields();
    },
    handleSubmit2(ev) {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          login(this.ruleForm).then(res => {
            if (res.code == 200) {
              sessionStorage.setItem("userinfo",JSON.stringify(res.data))
              if(res.data.isbind){
                this.$message({
                  type:'success',
                  message: res.msg
                })
                this.$router.push("/main")
              }else{
                this.$router.push({ path: "/bind" })
              }
            } else {
              this.$message({
                message: res.msg,
                type: 'error'
              });
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    getCode() {
      apisms({ mobile: this.ruleForm.mobile }).then(res => {
        if (res.code == 200) {
          this.$message({
            type: "success",
            message: ""
          });
        } else if(res.code == 200) {
          this.$message({
            type: "success",
            message: ""
          });
        } else {
          this.$message.error(res.msg);
        }
      });
      this.isdisable = true;
      this.sendCode();
    },
    sendCode() {
      // var timer_num = 60;
      var timeClock = setInterval(() => {
        this.numberCode--;
        this.btnmsg = this.numberCode + "";
        if (this.numberCode == 0) {
          clearInterval(timeClock);
          this.numberCode = 60;
          this.isdisable = false;
          this.btnmsg = "";
        }
      }, 1000);
    }
  }
};
</script>

<style lang="scss" scoped>
.login {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: skyblue;
  .headertitle {
    margin-top: 100px;
    text-align: center;
    font-size: 40px;
    line-height: 100px;
    color: white;
  }
}
.login-container {
  /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  margin: 50px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
}
</style>


