<template>
    <section class="chart-container">
        <el-row>
            <el-row class="title">
                <el-col :span="6"></el-col>
                <el-col :span="18" class="lableText">
                    <el-date-picker
                        v-model="dateTime"
                        type="datetimerange"
                        :picker-options="pickerOptions"
                        format="yyyy-MM-DD HH:mm"
                        @change="getDate(dateTime)"
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        align="right">
                    </el-date-picker>
                    :
                    <el-select v-model="params.status" placeholder="">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    <el-button type="primary" @click="queryData()"></el-button>
                </el-col>
            </el-row>
            <el-col :span="24"  v-loading="loading">
                <el-row class="chartsTitle">
                    
                </el-row>
                <el-row class="radioS">
                    <el-col :span="2" class="labeltitle"></el-col>
                    <el-col :span="12">
                         <el-radio-group v-model="radio" @change="changeType(radio)">
                            <el-radio :label="1"></el-radio>
                            <el-radio :label="2"></el-radio>
                            <el-radio :label="3"></el-radio>
                        </el-radio-group>
                    </el-col>
                </el-row>
                <div id="chartBar" style="width:100%;height:80vh"></div>
            </el-col>
        </el-row>
    </section>
</template>

<script>
    import echarts from 'echarts'
    import { consumptionlevel, inquirecity } from '../../api/index'
    export default {
        data() {
            return {
                chartBar: null,
                loading: true,
                dateTime: '',
                radio:1,
                params:{
                    mobile: '',
                    startTime:'',
                    endTime:'',
                    status:'all'
                },
                newList: [],
                oldList:[],
                newAll:[],
                oldALL:[],
                pickerOptions: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                ylable:[
                    '1-10','10-15','15-25','25-40','40-70','70-100','100-150','150-200','200-300', '300-500',
                    '500-700','700-1000','1000-1500','1500-2000','2000-3000','3000-4000','4000-5000','5000-6000','6000'
                ],
                options: [{
                    value: 'all',
                    label: ''
                    }, {
                    value: 'new',
                    label: ''
                    }, {
                    value: 'old',
                    label: ''
                }],
            }
        },
        created(){
            let userInfo = JSON.parse(sessionStorage.getItem('userinfo'))
            this.params.mobile = userInfo.mobile
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 200);
            this.params.startTime = this.$moment(start).format('YYYY-MM-DD HH:mm')
            this.params.endTime = this.$moment(end).format('YYYY-MM-DD HH:mm')
            this.dateTime= [this.params.startTime, this.params.endTime]
        },
        methods: {
            getDate(getDate){
                this.params.startTime = this.$moment(getDate[0]).format('YYYY-MM-DD HH:mm')
                this.params.endTime = this.$moment(getDate[1]).format('YYYY-MM-DD HH:mm')
            },
            getDataList(){
                consumptionlevel(this.params).then((res)=>{
                    if(res.code == 200){
                        this.loading = false
                        this.newAll = res.data.new
                        this.oldALL = res.data.old
                        if(this.radio == 1){
                            this.newList = res.data.new.clent_num
                            this.oldList = res.data.old.clent_num
                        } else if(this.radio == 2) {
                            this.newList = res.data.new.pay_total
                            this.oldList = res.data.old.pay_total
                        } else{
                            this.newList = res.data.new.pay_num
                            this.oldList = res.data.old.pay_num
                        }
                        this.drawBarChart()
                    }
                })
            },
            queryData(){
                this.loading = true
                this.getDataList()
            },
            changeType(radio){
                if(radio == 1){
                    this.newList = this.newAll.clent_num
                    this.oldList = this.oldALL.clent_num
                } else if(radio == 2) {
                    this.newList = this.newAll.pay_total
                    this.oldList = this.oldALL.pay_total
                } else{
                    this.newList = this.newAll.pay_num
                    this.oldList = this.oldALL.pay_num
                }
                this.drawBarChart()
            },
            drawBarChart() {
                this.chartBar = echarts.init(document.getElementById('chartBar'));
                var option = {
                    tooltip : {
                        trigger: 'axis',
                        axisPointer : {           // 
                            type : 'shadow'        // 'line' | 'shadow'
                        }
                        
                    },
                    color: [
                        '#1aa1ff',
                        '#31c17b',
                    ],
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'value',
                        show: true,
                        splitLine:{
                            show: true
                        },
                        axisLabel:{
                            show: false
                        },
                        axisLine:{
                            show: false
                        }
                    },
                    yAxis: {
                        type: 'category',
                        data: this.ylable
                    },
                    series: [
                        {
                            name: '',
                            type: 'bar',
                            stack: '',
                            label: {
                                normal: {
                                    show: true,
                                    position: 'insideRight'
                                }
                            },
                            data: this.newList
                        },
                        {
                            name: '',
                            type: 'bar',
                            stack: '',
                            label: {
                                normal: {
                                    show: true,
                                    position: 'insideRight'
                                }
                            },
                            data: this.oldList
                        }
                    ]
                }
                this.chartBar.setOption(option);
            }
        },
        mounted() {
            this.getDataList()
        }
    }
</script>

<style scoped lang="scss">
    .chart-container {
        width: 100%;
        float: left;
    }
    #chartBar{
        height: auto;
    }
    .title{
        height: 60px;
        line-height: 60px;
        font-size: 20px;
        padding: 0;
        font-weight: bold;
        background: #f2f2f2;
        padding: 0 30px;
        .lableText{
            text-align: right;
            font-weight:normal;
            font-size: 14px;  
        }
    }
    .chartsTitle{
        font-size: 16px;
        padding: 10px 30px;
    }
    .radioS{
        font-size: 14px;
        padding: 10px 30px;

    }
</style>



