<template>
	<section>
		<el-upload
			drag
			ref="upload"
			name="excel"
			class="upload-demo" 
			action="http://47.104.162.109/api/getExcel" 
			:data="params"
			:on-preview="handlePreview"
			accept=".xls,.xlsx"
			:auto-upload="false"
			:on-change="handleChange"
			:file-list="fileList"
			:on-error="handleError"
			:on-success="handleSuccess"
			multiple>
			<i class="el-icon-upload"></i>
			<div class="el-upload__text"><em></em></div>
			<div class="el-upload__tip" slot="tip">xls/xlsx</div>
		</el-upload>
		<!-- <el-button size="small" type="primary"></el-button> -->
	</section>
</template>

<script>
import {getExcel} from '../../api/index'
export default {
	props: {

	},
	data() {
		return {
			params:{
				mobile:''
			},
			fileList:[],
			formData: {},
			isConfirm: true
		};
	},
	computed: {
// http://47.104.162.109/api/getExcel
	},
	created() {
		let userInfo = JSON.parse(sessionStorage.getItem('userinfo'))
		this.params.mobile = userInfo.mobile
	},
	mounted() {
	},
	watch: {

	},
	methods: {
		handleRemove(file, fileList) {
			console.log(file, fileList);
		},
		handlePreview(file) {
			console.log(file);
		},
		handleError(err, file) {
			this.$message({
					type:"error",
					message:''
				})
			this.fileList = []
		},
		handleSuccess(res, file) {
			console.log(res)
			if(res.code == 200){
				this.$message({
					type:"success",
					message:''
				})
				this.fileList = []

				this.$router.push({path:'/table'})
			} else {
				this.$message({
					type:"error",
					message:res.msg
				})
				this.fileList = []

			}
		},
		async handleChange() {  
			if (!this.isConfirm) {    
				this.isConfirm = true
				return
			}
			await this.$confirm(', ?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			}).then(() => {    
				this.$refs.upload.submit()
				this.isConfirm = false
			}).catch(() => { 
				return true        
			}) 
			// if (bo) {    
				    
			// 	this.isConfirm = false
			// } else {    
			// 	this.fileList = []
			// }
		}
	},
	components: {

	},
};
</script>

<style scoped lang="scss">

</style>



