<template>
	<section>
		
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<el-row class="tableList">
			<el-table v-loading="loading" :data="tableData" height="73vh" border stripe>
				<el-table-column prop="id" label="" width="50"></el-table-column>
				<el-table-column prop="discountname" label="" width="150"></el-table-column>
				<el-table-column label="" width="100">
				<template slot-scope="scope">
					<span v-if="scope.row.discounttype==1"></span>
					<span v-if="scope.row.discounttype==2"></span>
					<span v-if="scope.row.discounttype==3"></span>
				</template>
				</el-table-column>
				<el-table-column prop="faceValue" label="" width="100"></el-table-column>
				<el-table-column prop="fullreduction" label="" width="100"></el-table-column>
				<el-table-column label="" width="100">
				<template slot-scope="scope">
					<span v-if="scope.row.integral">{{scope.row.integral}}</span>
					<span v-else>--</span>
				</template>
				</el-table-column>
				<el-table-column label="">
				<template slot-scope="scope">
					<span>{{scope.row.shopCityName}}</span><br>
					<span>{{scope.row.shopName}}</span><br>
					<span v-if="scope.row.projectType == 1"> </span><br>
					<span v-if="scope.row.projectType == 2"> </span><br>
					<span>{{scope.row.projectName}}</span>
				</template>
				</el-table-column>
				<!-- <el-table-column label="" width="100">
				<template slot-scope="scope">
					<span v-if="scope.row.position==1"></span>
					<span v-else></span>
				</template>
				</el-table-column> -->
				<el-table-column prop="describe" label=""></el-table-column>
				<el-table-column label="">
				<template slot-scope="scope">
					<span>{{scope.row.startTime}}</span>
					<span>{{scope.row.endTime}}</span>
				</template>
				</el-table-column>
				<el-table-column width="300" label="">
				<template slot-scope="scope">
					<el-button type="primary" @click="edit(scope.row.id)"></el-button>
					<el-button
					v-if="scope.row.states == 1"
					type="success"
					@click="downdataBtn(scope.row.id)"
					></el-button>
					<el-button
					v-if=" scope.row.states == 0"
					type="warning"
					@click="updataBtn(scope.row.id)"
					></el-button>
					<el-button type="danger" @click="removeList(scope.row.id)"></el-button>
				</template>
				</el-table-column>
			</el-table>
			</el-row>
			<el-row class="pageination">
			<el-col>
				<el-pagination
				style="textAlign:center;marginTop:10px"
				@size-change="handleSizeChange"
				@current-change="handleCurrentChange"
				:current-page.sync="currentPage"
				:page-sizes="[30, 50, 100, 150]"
				:page-size="20"
				layout="total, sizes, prev, pager, next, jumper"
				:total="this.total"
				></el-pagination>
			</el-col>
			</el-row>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	// import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';
	import { inquirecity } from '../../api/index'
	export default {
		data() {
			return {
				params:{
					current_page: 1,
					size: 40
				},
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//
				tableData:[],
				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id: 0,
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				}

			}
		},
		methods: {
			//
			getSwitchList(){
				inquirecity(this.params).then((res)=>{
					console.log(res)
				})
			},
			//
			handleSizeChange(val) {
				this.params.size = val;
				this.params.page = 1;
				this.currentPage = 1;
				this.getSwitchList();
			},
			handleCurrentChange(val) {
				this.params.page = val;
				this.getSwitchList();
			},
		},
		mounted() {
			this.getSwitchList()
		}
	}

</script>

<style scoped lang="scss">
.app-container {
  position: relative;
  height: auto;
  .newAdd {
    height: 50px;
    line-height: 50px;
    text-align: right;
    // margin: 10px 0;
  }
  .tableList {
    width: 100%;
    position: absolute;
    top: 60px;
    height: 73vh;
    overflow-y: auto;
  }
  .pageination {
    position: absolute;
    top: 82vh;
    left: 0;
    right: 0;
  }
}
</style>


