<template>
	<section>
		<el-tree
			:data="data"
			show-checkbox
			default-expand-all
			node-key="id"
			ref="tree"
			highlight-current
			@check-change="getData"
			:props="defaultProps">
		</el-tree>
		<div class="buttons">
			<el-button @click="getCheckedNodes"> node </el-button>
			<el-button @click="getCheckedKeys"> key </el-button>
			<el-button @click="setCheckedNodes"> node </el-button>
			<el-button @click="setCheckedKeys"> key </el-button>
			<el-button @click="resetChecked"></el-button>
		</div>
	</section>
</template>

<script>
	export default {
		data() {
			return {
				data: [{
				id: 1,
				label: ' 1',
				children: [{
					id: 4,
					label: ' 1-1',
					children: [{
					id: 9,
					label: ' 1-1-1'
					}, {
					id: 10,
					label: ' 1-1-2'
					}]
				}]
				}, {
				id: 2,
				label: ' 2',
				children: [{
					id: 5,
					label: ' 2-1'
				}, {
					id: 6,
					label: ' 2-2'
				}]
				}, {
				id: 3,
				label: ' 3',
				children: [{
					id: 7,
					label: ' 3-1'
				}, {
					id: 8,
					label: ' 3-2'
				}]
				}],
				defaultProps: {
				children: 'children',
				label: 'label'
				}
			}
		},
		methods: {
			getData(){
				let res = this.$refs.tree.getHalfCheckedKeys().concat(this.$refs.tree.getCheckedKeys())	
				console.log(res)
			},
			getCheckedNodes() {
				console.log(this.$refs.tree.getCheckedNodes());
			},
			getCheckedKeys() {
				console.log(this.$refs.tree.getCheckedKeys());
			},
			setCheckedNodes() {
				this.$refs.tree.setCheckedNodes([{
				id: 5,
				label: ' 2-1'
				}, {
				id: 9,
				label: ' 1-1-1'
				}]);
			},
			setCheckedKeys() {
				this.$refs.tree.setCheckedKeys([3]);
			},
			resetChecked() {
				this.$refs.tree.setCheckedKeys([]);
			}
		},
	}

</script>

<style scoped>

</style>


