<template>
    <section class="chart-container">
        <el-row>
            <el-row class="title">
                <el-col :span="6"></el-col>
                <el-col :span="18" class="lableText">
                    <el-date-picker
                        v-model="dateTime"
                        type="datetimerange"
                        :picker-options="pickerOptions"
                        format="yyyy-MM-DD HH:mm"
                        @change="getDate(dateTime)"
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        align="right">
                    </el-date-picker>
                    :
                    <el-select v-model="params.status" placeholder="">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    <el-button type="primary" @click="queryData()"></el-button>
                </el-col>
            </el-row>
            <el-row class="detile">
                <el-row class="chartsTitle">
                    
                </el-row>
                <el-row class="radioS">
                    <el-col :span="1" class="labeltitle">:</el-col>
                    <el-col :span="6">
                         <el-radio-group v-model="radio" @change="changeType(radio)">
                            <el-radio :label="1"></el-radio>
                            <el-radio :label="2"></el-radio>
                            <el-radio :label="3"></el-radio>
                        </el-radio-group>
                    </el-col>
                </el-row>
            </el-row>
            <el-row v-loading="loading">
                <div id="chartBar" style="width:100%;height:300px"></div>
            </el-row>
            <el-row>
                <el-table
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column
                    prop="lable"
                    label=""
                    width="180">
                    </el-table-column>
                    <el-table-column
                    prop="clent_num"
                    label=""
                    width="180">
                    </el-table-column>
                    <el-table-column
                    prop="clent_num_pct"
                    label="">
                    </el-table-column>
                    <el-table-column
                    prop="pay_num"
                    label="">
                    </el-table-column>
                    <el-table-column
                    prop="pay_avg"
                    label="">
                    </el-table-column>
                    <el-table-column
                    prop="pay_total"
                    label="">
                    </el-table-column>
                    <el-table-column
                    prop="pay_total_pct"
                    label="">
                    </el-table-column>
                </el-table>
            </el-row>
        </el-row>
    </section>
</template>

<script>
    import echarts from 'echarts'
    import { consumpquency } from '../../api/index'
    export default {
        data() {
            return {
                loading:true,
                chartBar: null,
                dateTime: [],
                radio:1,
                params:{
                    mobile: '',
                    startTime:'',
                    endTime:'',
                    status:'all'
                },
                radioAllList:[],
                ylable:[
                    '1','2','3','4','5'
                ],
                peopleData:[],
                pickerOptions: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                tableData: [],
                options: [{
                    value: 'all',
                    label: ''
                    }, {
                    value: 'new',
                    label: ''
                    }, {
                    value: 'old',
                    label: ''
                }],
                value: ''
            }
        },
        created(){
            let userInfo = JSON.parse(sessionStorage.getItem('userinfo'))
            this.params.mobile = userInfo.mobile
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 200);
            this.params.startTime = this.$moment(start).format('YYYY-MM-DD HH:mm')
            this.params.endTime = this.$moment(end).format('YYYY-MM-DD HH:mm')
            this.dateTime= [this.params.startTime, this.params.endTime]
        },
        methods: {
            getDataList(){
                consumpquency(this.params).then((res)=>{
                    if(res.code == 200){
                        this.loading = false
                        if(this.params.status == 'all'){
                            this.tableData = res.data.all.form
                            this.radioAllList = res.data.all.histogram
                        } else{
                            this.tableData = res.data.newOrold.form
                            this.radioAllList = res.data.newOrold.histogram
                        }

                        if(this.radio == 1){
                            this.peopleData = this.radioAllList.clent_num
                        } else if(this.radio == 2){
                            this.peopleData = this.radioAllList.pay_num
                        } else if(this.radio == 3){
                            this.peopleData = this.radioAllList.pay_total
                        }
                        console.log(res)
                        this.drawBarChart()
                    }
                })
            },
            queryData(){
                this.loading = true
                this.getDataList()
            },
            getDate(getDate){
                this.params.startTime = this.$moment(getDate[0]).format('YYYY-MM-DD HH:mm')
                this.params.endTime = this.$moment(getDate[1]).format('YYYY-MM-DD HH:mm')
            },
            changeType(radio){
                if(this.radio == 1){
                    this.peopleData = this.radioAllList.clent_num
                } else if(this.radio == 2){
                    this.peopleData = this.radioAllList.pay_num
                } else if(this.radio == 3){
                    this.peopleData = this.radioAllList.pay_total
                }
                this.drawBarChart()
            },
            drawBarChart() {
                this.chartBar = echarts.init(document.getElementById('chartBar'));
                var option = {
                    tooltip : {
                        trigger: 'axis',
                        axisPointer : {           // 
                            type : 'shadow'        // 'line' | 'shadow'
                        }
                        
                    },
                    color: [
                        '#1aa1ff',
                        '#31c17b',
                    ],
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'value',
                        show: true,
                        splitLine:{
                            show: true
                        },
                        axisLabel:{
                            show: false
                        },
                        axisLine:{
                            show: false
                        }
                    },
                    yAxis: {
                        type: 'category',
                        data: this.ylable
                    },
                    series: [
                        {
                            name: '',
                            type: 'bar',
                            stack: '',
                            label: {
                                normal: {
                                    show: true,
                                    position: 'insideRight'
                                }
                            },
                            data: this.peopleData
                        }
                        // ,
                        // {
                        //     name: '',
                        //     type: 'bar',
                        //     stack: '',
                        //     label: {
                        //         normal: {
                        //             show: true,
                        //             position: 'insideRight'
                        //         }
                        //     },
                        //     data: [120, 132, 101, 134, 90]
                        // }
                    ]
                }
                this.chartBar.setOption(option);
            }
        },
        mounted() {
            this.getDataList()
        }
    }
</script>

<style scoped lang="scss">
    .chart-container {
        width: 100%;
        float: left;
    }
    #chartBar{
        height: auto;
    }
    .title{
        height: 60px;
        line-height: 60px;
        font-size: 20px;
        padding: 0;
        font-weight: bold;
        background: #f2f2f2;
        padding: 0 30px;
        .lableText{
            text-align: right;
            font-weight:normal;
            font-size: 14px;  
        }
    }
    .detile{
        padding: 10px 0;
    }
    .chartsTitle{
        font-size: 16px;
        padding: 10px 30px;
    }
    .radioS{
        font-size: 14px;
        padding: 10px 30px;

    }
</style>



