<template>
    <section class="chart-container">
        <el-row>
            <el-row class="title">
                <el-col :span="6"></el-col>
                <el-col :span="18" class="lableText">
                    <el-date-picker
                        v-model="dateTime"
                        type="datetimerange"
                        :picker-options="pickerOptions"
                        format="yyyy-MM-DD HH:mm"
                        @change="getDate(dateTime)"
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        align="right">
                    </el-date-picker>
                    :
                    <el-select v-model="params.userType" placeholder="">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    <el-button type="primary" @click="queryData()"></el-button>
                </el-col>
            </el-row>
            <el-row class="detile">
                <el-row class="chartsTitle">
                    
                </el-row>
                <el-row class="radioS">
                    <el-col :span="2" class="labeltitle">:</el-col>
                    <el-col :span="3">
                         <el-radio-group v-model="params.time" @change="changeTime(params.time)">
                            <el-radio :label="10">10</el-radio>
                            <el-radio :label="30">30</el-radio>
                        </el-radio-group>
                    </el-col>
                    <el-col :span="1" class="labeltitle">:</el-col>
                    <el-col :span="6">
                         <el-radio-group v-model="params.target"  @change="changeType(params.target)">
                            <el-radio :label="1">M</el-radio>
                            <el-radio :label="2">F</el-radio>
                        </el-radio-group>
                    </el-col>
                    <el-col :offset="10" :span="2">
                        <el-button type="primary" @click="daochuData()"></el-button>
                    </el-col>
                </el-row>
            </el-row>
            <el-row>
                <el-table
                    :data="tableData"
                    :header-cell-style="{background:'#f2f2f2'}"
                    v-loading="loading"
                    style="width: 100%">
                    <!-- <el-table-column
                    prop="date"
                    label=""
                    width="180">
                    </el-table-column> -->
                    <el-table-column
                    prop="count"
                    label="">
                    </el-table-column>
                    <el-table-column
                    prop="kehuzhanbi"
                    label="">
                    </el-table-column>
                    <el-table-column
                    prop="leijizhanbi"
                    label="">
                    </el-table-column>
                    <el-table-column
                    prop="renjunxiaofei"
                    label="())">
                    </el-table-column>
                    <el-table-column
                    v-if="showtarge"
                    prop="huigouyici"
                    label="">
                    </el-table-column>
                    <el-table-column
                    prop="huigouerci"
                    v-if="showtarge"
                    label="">
                    </el-table-column>
                     <el-table-column
                    prop="huigousanci"
                    v-if="showtarge"
                    label="">
                    </el-table-column>
                     <el-table-column
                    prop="huigousici"
                    v-if="showtarge"
                    label="">
                    </el-table-column>
                     <el-table-column
                    prop="huigouwuci"
                    v-if="showtarge"
                    label="">
                    </el-table-column>
                </el-table>
            </el-row>
        </el-row>
    </section>
</template>

<script>
    import { inquireBuyBack, exportInquireBuyBack } from '../../api/index'
    import echarts from 'echarts'
    export default {
        data() {
            return {
                loading:true,
                chartBar: null,
                dateTime: '',
                showtarge: false,
                params:{
                    mobile: '',
                    startTime:'',
                    endTime:'',
                    userType: '1',
                    time:10,
                    target:1
                },
                pickerOptions: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                tableData: [],
                options: [{
                    value: '1',
                    label: ''
                    }, {
                    value: '2',
                    label: ''
                    }, {
                    value: '3',
                    label: ''
                }]
            }
        },
        created(){
            let userInfo = JSON.parse(sessionStorage.getItem('userinfo'))
            this.params.mobile = userInfo.mobile
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 200);
            this.params.startTime = this.$moment(start).format('YYYY-MM-DD HH:mm')
            this.params.endTime = this.$moment(end).format('YYYY-MM-DD HH:mm')
            this.dateTime= [this.params.startTime, this.params.endTime]
        },
        methods: {
            getDate(getDate){
                this.params.startTime = this.$moment(getDate[0]).format('YYYY-MM-DD HH:mm')
                this.params.endTime = this.$moment(getDate[1]).format('YYYY-MM-DD HH:mm')
            },
            getTableData(){
                this.loading = true
                inquireBuyBack(this.params).then(res=>{
                    if(res.code == 200){
                        this.tableData = res.data
                        this.loading = false
                        console.log(res)
                    } else{
                        this.$message({
                            message: res.msg,
                            type: 'error'
                        });
                    }
                   
                })
            },
            changeType(type){
                if(type == 1){
                    this.showtarge= false
                } else{
                    this.showtarge = true
                }
            },
            changeTime(time){
                this.params.time =  time
                this.getTableData()
            },
            queryData(){
                this.loading = true
                this.params.time = 10
                this.getTableData()
            },
            daochuData(){
                // exportInquireBuyBack(this.params).then((res)=>{
                //     console.log(res)
                // })
                window.location.href='http://47.104.162.109/api/exportInquireBuyBack?mobile='+this.params.mobile+'&startTime='+this.params.startTime+'&endTime='+this.params.endTime+'&userType='+this.params.userType+'&time='+this.params.time+'&target='+this.params.target
            }
        },
        mounted() {
            this.getTableData()
        }
    }
</script>

<style scoped lang="scss">
    .chart-container {
        width: 100%;
        float: left;
    }
    #chartBar{
        height: auto;
    }
    .title{
        height: 60px;
        line-height: 60px;
        font-size: 20px;
        padding: 0;
        font-weight: bold;
        background: #f2f2f2;
        padding: 0 30px;
        .lableText{
            text-align: right;
            font-weight:normal;
            font-size: 14px;  
        }
    }
    .detile{
        padding: 10px 0;
    }
    .chartsTitle{
        font-size: 16px;
        padding: 10px 30px;
    }
    .radioS{
        font-size: 14px;
        padding: 10px 30px;

    }
</style>



